body {
  
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background: -webkit-linear-gradient(1deg, #e66465, #9198e5);
  background: linear-gradient(89deg, #e66465, #9198e5);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;
}

.App {
  text-align: center;
}
.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.backdrop {
	position: fixed;
	z-index: 100;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.5);
}


.backdrop-open {
opacity: 1;
-webkit-transform: translateY(0);
        transform: translateY(0);
-webkit-transition: all .5s ease-out;
transition: all .5s ease-out;

}

.backdrop-closed {
opacity: 0;
-webkit-transform: translateY(-100%);
        transform: translateY(-100%);
}
.spinner {
	position: fixed;
	top: 40%;
  left: 47%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
  width: 4rem;
	height: 4rem;
  padding: 10px;
  border: 1px solid #eee;
	border-radius: 50%;
  box-shadow: 0 2px 2px #ccc;
  z-index: 200;
	background: -webkit-linear-gradient(1deg, #e66465, #9198e5);
	background: linear-gradient(89deg, #e66465, #9198e5);
  text-align: center;
  box-sizing: border-box;

	display: flex;
	justify-content: center;
	align-items: center;
}

.spinner-rotor {
	-webkit-animation: revolution 5s linear infinite;
	        animation: revolution 5s linear infinite;
}

.spinner img {
	width: 100%;
}

.spinner-text {
	position: fixed;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	border-right: 2px solid white;
	color: white;
	font-weight: bold;
	font-size: 2rem;
	z-index: 200;
	-webkit-animation: typewriter 3s steps(12) .5s infinite alternate, blinkTextCursor 0.5s infinite;
	        animation: typewriter 3s steps(12) .5s infinite alternate, blinkTextCursor 0.5s infinite;
	white-space: nowrap;
	overflow: hidden;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

@-webkit-keyframes revolution {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
 }

@keyframes revolution {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
 }

 @-webkit-keyframes typewriter {
	from {
		width: 0;
	}

	to {
		width: 210px;
	}
}

 @keyframes typewriter {
	from {
		width: 0;
	}

	to {
		width: 210px;
	}
}

@-webkit-keyframes blinkTextCursor {
	from {
		border-right-color: white;
	}

	to {
		border-right-color: transparent;
	}
}

@keyframes blinkTextCursor {
	from {
		border-right-color: white;
	}

	to {
		border-right-color: transparent;
	}
}
.button {
  padding: 0px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
